.Header {
  height: 900px;
  
}
.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 70px;
}

.logo {
  font-size: 50px;
  font-weight: bold;
}

.pinLogoContainer {
  display: inline-block; /* 인라인 블록으로 설정하여 텍스트와 함께 정렬 */
  vertical-align: middle; /* 텍스트와 수직으로 정렬 */
  margin: 0 5px; /* 필요에 따라 여백 조정 */
}

.pinLogo {
  width: 20px; /* 로고 크기 조정 (원하는 크기로 설정) */
  height: auto; /* 비율 유지 */
}

.search-bar {
  width: 840px;
  height: 40px;
  align-items: center;
  padding: 10px;
  border-radius: 20px;
  border: 3px solid #C9BAD3;
}

.profile-container {
  display: flex;
  align-items: center;
  padding: 20px 0;
}

.profile-image {
  border-radius: 80%;
  width: 180px;
  height: 180px;
  margin-left: 180px;
  margin-bottom: 100px;
}

.profile-details {
  font-size: 18px;
}

.profile-name {
  margin-left: 50px;
  margin-top: -50px;
}

.bio {
  font-size: 14px;
  margin-left: 50px;
  margin-top: -10px;
  color: gray;
}

.playlist-container {
  display: flex;
  gap: 20px;
  overflow-x: auto; /* 가로 스크롤을 허용하여 넘치면 스크롤 */
  background: linear-gradient(to right, #C8D5E3, #F6E3E7, #F5E8F1);
}

.playlist-card {
  margin-top: 15px;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
}

.playlist-image {
  width: 90%;
  border-radius: 20px;
}

.playlist-title {
  margin: 10px 0;
  font-size: 16px;
}

.playlist-description {
  font-size: 12px;
  color: gray;
}
